import React from 'react';
import axios from 'axios';
import TextField from '@material-ui/core/TextField';
import Toast from 'react-bootstrap/Toast';

const authKey = sessionStorage.getItem('access_token');
const CostumerDetails_Server = `getcostumer/${authKey}/`;
const CollectionList_Server = `getcollection/${authKey}/`;
const updateCostumerDetails_Server = `updatecostumer/`;
const CollectionListDetail_Server = `getcollectiondetail/`;
const createCollectionList_Server = `createcollection/${authKey}/`;
const GetCollectionbyDate = `getcollectionbydate/${authKey}/`;
const updateCollectionList_Server = `updatecollection/`;
const costumerdetailinfo = `costumerdetail/`;
const date1 = localStorage.getItem('date');

export default function CollectionEntryComponent1() {
	const [costumerDetails, setCostumerDetails] = React.useState([]);
	const [date, setDate1] = React.useState('');
	const [newAmmount, setNewAmmount] = React.useState('');
	const [loadingCollection, setLoadingCollection] = React.useState(false);
	const [loadCollection, setLoadCollection] = React.useState([]);
	const inputRefs = React.useRef([]);
	const [showalert, setShowalert] = React.useState(false);
	const [alertmessage, setAlertMessage] = React.useState(
		'Ammount Balance: $0.00',
	);

	const sleep = (milliseconds) => {
		return new Promise((resolve) => setTimeout(resolve, milliseconds));
	};

	const AddCollection = async (details, id) => {
		const collectedammout = parseInt(details.loanammount / 100);
		let updated_balance = parseInt(details.ammount_Balance) - collectedammout;
		let records = {
			name: details.name,
			loanammount: details.loanammount,
			loan_date: details.loan_date,
			loanInterst: details.loanInterst,
			ms: details.ms,
			place: details.place,
			ammount_Balance: updated_balance,
			place_id: details.place_id,
		};
		let collection = {
			date: date,
			costumer_id: details.id,
			costumer_name: details.name,
			ammount: collectedammout,
		};

		// console.log(collection)

		await axios.post(createCollectionList_Server, collection).then(
			(res) => {
				axios
					.post(`${updateCostumerDetails_Server}${details.id}/`, records)

					.then(
						(res) => {
							console.log('updated');
						},
						(err) => {
							console.log('error put', id);
						},
					);
			},
			(error) => {
				console.log(error, 'error');
			},
		);
	};

	const LoadCollectionFunction = async () => {
		await axios.get(`${GetCollectionbyDate}${date}/`).then((res) => {
			setLoadCollection(res.data);
		});
	};

	const getCostumerDetails = async () => {
		await axios.get(CostumerDetails_Server).then((res) => {
			setCostumerDetails(res.data);
		});
	};

	const ADD_TO_DB = async () => {
		await costumerDetails
			.sort((a, b) => {
				if (a.place_id < b.place_id) return -1;
				if (a.place_id > b.place_id) return 1;
				return 0;
			})
			.map((data) => {
				AddCollection(data, data.id);
			});
		sleep(2000);
	};

	const callDatas = async () => {
		setLoadingCollection(true);
		await ADD_TO_DB();
		LoadCollectionFunction();
		setLoadingCollection(false);
	};

	const getBalance = async (id) => {
		setShowalert(true);
		await axios
			.get(`${costumerdetailinfo}${id}/`)
			.then((res) =>
				setAlertMessage(
					`The Balance of ${res.data.name} is INR: ${res.data.ammount_Balance}`,
				),
			);
	};

	React.useEffect(() => {
		getCostumerDetails();
	}, [date]);

	const updateCollection = async (e, data, id) => {
		let costumerData = await axios.get(
			`${costumerdetailinfo}${data.costumer_id}/`,
		);
		if (e.key === 'Enter') {
			let costumerAmmount_balance = costumerData.data.ammount_Balance;
			if (parseInt(e.target.value) > parseInt(data.ammount)) {
				let diff = parseInt(e.target.value) - parseInt(data.ammount);
				costumerAmmount_balance =
					parseInt(costumerAmmount_balance) - parseInt(diff);
			} else if (parseInt(e.target.value) < parseInt(data.ammount)) {
				let diff = parseInt(data.ammount) - parseInt(e.target.value);
				costumerAmmount_balance =
					parseInt(costumerAmmount_balance) + parseInt(diff);
			} else {
				costumerAmmount_balance = costumerData.data.ammount_Balance;
			}
			costumerData = await costumerData.data;
			let NewCostumerData = await {
				name: costumerData.name,
				loanammount: costumerData.loanammount,
				loan_date: costumerData.loan_date,
				loanInterst: costumerData.loanInterst,
				ms: costumerData.ms,
				place: costumerData.place,
				ammount_Balance:
					typeof costumerAmmount_balance === undefined
						? costumerAmmount_balance
						: costumerAmmount_balance.toString(),
				place_id: costumerData.place_id,
			};
			await console.log(NewCostumerData);
			let newCollectiondata = await {
				date: date,
				costumer_id: data.costumer_id,
				costumer_name: data.costumer_name,
				ammount: e.target.value.toString(),
			};
			await console.log(newCollectiondata);

			await axios
				.post(
					`${updateCostumerDetails_Server}${data.costumer_id}/`,
					NewCostumerData,
				)
				.then((res) => {
					axios
						.post(`${updateCollectionList_Server}${data.id}`, newCollectiondata)
						.then((res) => console.log('Added'));
				});

			const nextIndex = (await id) + 1;
			(await inputRefs.current[nextIndex]) &&
				inputRefs.current[nextIndex].focus();
		}
	};

	function hideAlert() {
		setShowalert(false);
	}

	return (
		<div>
			<Toast
				show={showalert}
				className='d-flex flex-row gap-4 position-fixed end-0 top-0 bg-dark text-white'
				onClose={hideAlert}>
				<Toast.Body>{alertmessage}</Toast.Body>
				<button className='btn' onClick={hideAlert}>
					
				</button>
			</Toast>
			<input
				type='date'
				style={{
					width: 200,
					border: '1px solid gray',
					padding: 10,
					margin: 20,
				}}
				placeholder='Select Date'
				onChange={(e) => setDate1(e.target.value)}
			/>
			<button
				className='btn btn-primary'
				style={{
					margin: 10,
				}}
				onClick={callDatas}>
				Create Collection
			</button>
			<button
				className='btn btn-danger'
				style={{
					margin: 10,
				}}
				onClick={LoadCollectionFunction}>
				Load Collection
			</button>

			<div>
				{loadingCollection === false ? (
					<table className='table'>
						<thead className='thead-light'>
							<tr>
								<th>Name</th>
								<th>Ammount</th>
								<th>Balance</th>
							</tr>
						</thead>
						<tbody>
							{loadCollection.map((data, id) => (
								<tr>
									<td>{data.costumer_name}</td>
									<td>
										<TextField
											label='Ammount'
											inputProps={{
												onKeyPress: (e) => updateCollection(e, data, id),
											}}
											placeholder={data.ammount}
											inputRef={(ref) => (inputRefs.current[id] = ref)}
											onFocus={(e) => e.target.select()}
											color='secondary'
											defaultValue={data.ammount}
										/>
									</td>
									<td>
										<p
											className='btn btn-primary'
											onClick={() => getBalance(data.costumer_id)}>
											Get Balance
										</p>
									</td>
								</tr>
							))}
						</tbody>
					</table>
				) : loadCollection.length === 0 ? (
					<p
						style={{
							width: '100%',
							height: '100%',
							display: 'flex',
							justifyContent: 'center',
							padding: '100px',
						}}>
						No Records Found Create Collection
					</p>
				) : (
					<div
						style={{
							width: '100%',
							height: '100%',
							display: 'flex',
							justifyContent: 'center',
							padding: '100px',
						}}>
						<div className='spinner-border text-primary' role='status'>
							<span className='visually-hidden'>Loading...</span>
						</div>
					</div>
				)}
			</div>
		</div>
	);
}
