import { useState, useEffect } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import noData from '../assests/nodata.webp';

const authKey = sessionStorage.getItem('access_token');
const COSTUMERCLOSEHIGH = `getcloseup`;
const COSTUMERCLOSELOW = `getclosedown`;
const other_ammount_inServer = `getoai`;
const inversmentServer = `getinversment`;
const other_ammount_outServer = `getoao`;
const othersServer = `getothers`;
const deletecloseup = `deletecloseup`;
const deleteclosedown = `deleteclosedown`;
const deleteoai = `deleteoai`;
const deleteinversment = `deleteinversment`;
const deleteoao = `deleteoao`;
const deleteothers = `deleteothers`;
const getCosURL = `getcostumer/${authKey}`;

export default function EditPlanDataComponent() {
	const [closeUp, setCloseUp] = useState([]);
	const [closeDown, setCloseDown] = useState([]);
	const [otherAmmountIn, setOtherAmmountIn] = useState([]);
	const [otherAmmountout, setOtherAmmountout] = useState([]);
	const [others, setOthers] = useState([]);
	const [Inversment, setInversment] = useState([]);
	const [showData, setShowData] = useState('');
	const [closedLoans, setClosedLoans] = useState([]);

	const getCloseUp = async () => {
		const res = await axios.get(COSTUMERCLOSEHIGH);
		var filteredData = await res.data.filter((data) =>
			data.date.includes(localStorage.getItem('date')),
		);
		await setCloseUp(filteredData);
	};
	const getCloseDown = async () => {
		const res = await axios.get(COSTUMERCLOSELOW);
		var filteredData = await res.data.filter((data) =>
			data.date.includes(localStorage.getItem('date')),
		);
		await setCloseDown(filteredData);
	};
	const getAmmountIn = async () => {
		const res = await axios.get(other_ammount_inServer);
		var filteredData = await res.data.filter((data) =>
			data.date.includes(localStorage.getItem('date')),
		);
		await setOtherAmmountIn(filteredData);
	};
	const getAmmountOut = async () => {
		const res = await axios.get(other_ammount_outServer);
		var filteredData = await res.data.filter((data) =>
			data.date.includes(localStorage.getItem('date')),
		);
		await setOtherAmmountout(filteredData);
	};
	const getInversment = async () => {
		const res = await axios.get(inversmentServer);
		var filteredData = await res.data.filter((data) =>
			data.date.includes(localStorage.getItem('date')),
		);
		await setInversment(filteredData);
	};
	const getOthers = async () => {
		const res = await axios.get(othersServer);
		var filteredData = await res.data.filter((data) =>
			data.date.includes(localStorage.getItem('date')),
		);
		await setOthers(filteredData);
	};

	const getClosedLoans = async () => {
		const response = await axios.get(getCosURL);
		var closed = await response.data.filter(
			(data) => data.ammount_Balance === '0',
		);
		await setClosedLoans(closed);
	};

	useEffect(async () => {
		var cc = '';
		await closedLoans.map((data) => {
			cc =
				cc +
				'\nname : ' +
				data.name +
				' , place :' +
				data.place +
				' , ' +
				data.id +
				' , ';
		});
		await sessionStorage.setItem('cc', cc);
	}, [closedLoans]);

	useEffect(() => {
		callDatas();
	}, [showData]);

	const callDatas = async () => {
		await getCloseUp();
		await getCloseDown();
		await getAmmountIn();
		await getAmmountOut();
		await getInversment();
		await getOthers();
		await getClosedLoans();
	};

	const deleteCloseUp = (id) => {
		axios
			.delete(`${deletecloseup}/${id}`)
			.then(() => toast.error('Deleted Successfully'));
		callDatas();
	};

	const deleteCloseDown = (id) => {
		axios
			.delete(`${deleteclosedown}/${id}`)
			.then(() => toast.error('Deleted Successfully'));
		callDatas();
	};
	const deleteInversmentData = (id) => {
		axios
			.delete(`${deleteinversment}/${id}`)
			.then(() => toast.error('Deleted Successfully'));
		callDatas();
	};

	const deleteOAO = (id) => {
		axios
			.delete(`${deleteoao}/${id}`)
			.then(() => toast.error('Deleted Successfully'));
		callDatas();
	};

	const deleteOAI = (id) => {
		axios
			.delete(`${deleteoai}/${id}`)
			.then(() => toast.error('Deleted Successfully'));
		callDatas();
	};

	const deleteOOO = (id) => {
		axios
			.delete(`${deleteothers}/${id}`)
			.then(() => toast.error('Deleted Successfully'));
		callDatas();
	};

	return (
		<div>
			<div className='d-flex flex-row gap-2'>
				<button className='link-button' onClick={() => setShowData('cd')}>
					Show Closed Costumers data
				</button>
				<button className='link-button' onClick={() => setShowData('id')}>
					Show Inversment
				</button>
				<button className='link-button' onClick={() => setShowData('ai')}>
					Show Ammount IN
				</button>
				<button className='link-button' onClick={() => setShowData('ao')}>
					Show Ammount OUT
				</button>
				<button className='link-button' onClick={() => setShowData('oe')}>
					Show Other Expences Data
				</button>
				<button className='btn btn-danger' onClick={() => setShowData('cl')}>
					Show Closed Loans
				</button>
			</div>
			<div className='mt-4'>
				{showData === 'cd' ? (
					<div className='d-flex flex-row gap-3 justify-content-evenly p-3'>
						{closeUp.length > 0 ? (
							<div>
								<p className='h5 p-3 text-primary'> </p>
								<table className='table table-stripped table-bordered'>
									<thead className='th-dark'>
										<tr>
											<th>Name</th>
											<th>Ammount</th>
											<th>Del</th>
										</tr>
									</thead>
									<tbody>
										{closeUp.map((data) => (
											<tr key={data.id}>
												<td>{data.costumername}</td>
												<td>{data.ammount}</td>
												<td>
													<button
														className='btn btn-danger'
														onClick={() => deleteCloseUp(data.id)}>
														Del
													</button>
												</td>
											</tr>
										))}
									</tbody>
								</table>
							</div>
						) : (
							<div
								style={{
									backgroundImage: `url(${noData})`,
									width: 400,
									height: 400,
									display: 'flex',
									justifyContent: 'center',
									alignItems: 'center',
									textAlign: 'center',
								}}>
								<p className='h2 text-dark bg-light p-3 rounded '>
									No Data Found
								</p>
							</div>
						)}
						{closeDown.length > 0 ? (
							<div>
								<p className='h5 p-3 text-primary'> </p>
								<table className='table table-stripped table-bordered'>
									<thead className='th-dark'>
										<tr>
											<th>Name</th>
											<th>Ammount</th>
											<th>Delete</th>
										</tr>
									</thead>
									<tbody>
										{closeDown.map((data) => (
											<tr className='text-align-center' key={data.id}>
												<td>{data.costumername}</td>
												<td>{data.ammount}</td>
												<td>
													<button
														className='btn btn-danger'
														onClick={() => deleteCloseDown(data.id)}>
														Delete
													</button>
												</td>
											</tr>
										))}
									</tbody>
								</table>
							</div>
						) : (
							<div
								style={{
									backgroundImage: `url(${noData})`,
									width: 400,
									height: 400,
									display: 'flex',
									justifyContent: 'center',
									alignItems: 'center',
									textAlign: 'center',
								}}>
								<p className='h2 text-dark bg-light p-3 rounded '>
									No Data Found
								</p>
							</div>
						)}
					</div>
				) : showData === 'ai' ? (
					<div>
						<p className='h5 p-3 text-primary'> </p>
						<table className='table table-stripped table-bordered'>
							<thead className='th-dark'>
								<tr>
									<th>Name</th>
									<th>Ammount</th>
									<th>Delete</th>
								</tr>
							</thead>
							<tbody>
								{otherAmmountIn.map((data) => (
									<tr className='text-align-center' key={data.id}>
										<td>{data.reason}</td>
										<td>{data.ammount}</td>
										<td>
											<button
												className='btn btn-danger'
												onClick={() => deleteOAI(data.id)}>
												Del
											</button>
										</td>
									</tr>
								))}
							</tbody>
						</table>
					</div>
				) : showData === 'ao' ? (
					<div>
						<p className='h5 p-3 text-primary'> </p>
						<table className='table table-stripped table-bordered'>
							<thead className='th-dark'>
								<tr>
									<th>Name</th>
									<th>Ammount</th>
									<th>Delete</th>
								</tr>
							</thead>
							<tbody>
								{otherAmmountout.map((data) => (
									<tr className='text-align-center' key={data.id}>
										<td>{data.reason}</td>
										<td>{data.ammount}</td>
										<td>
											<button
												className='btn btn-danger'
												onClick={() => deleteOAO(data.id)}>
												Del
											</button>
										</td>
									</tr>
								))}
							</tbody>
						</table>
					</div>
				) : showData === 'oe' ? (
					<div>
						<p className='h5 p-3 text-primary'> </p>
						<table className='table table-stripped table-bordered'>
							<thead className='th-dark'>
								<tr>
									<th>Name</th>
									<th>Ammount</th>
									<th>Delete</th>
								</tr>
							</thead>
							<tbody>
								{others.map((data) => (
									<tr className='text-align-center' key={data.id}>
										<td>{data.reason}</td>
										<td>{data.ammount}</td>
										<td>
											<button
												className='btn btn-danger'
												onClick={() => deleteOOO(data.id)}>
												Del
											</button>
										</td>
									</tr>
								))}
							</tbody>
						</table>
					</div>
				) : showData === 'id' ? (
					<div>
						<p className='h5 p-3 text-primary'></p>
						<table className='table table-stripped table-bordered'>
							<thead className='th-dark'>
								<tr>
									<th>Name</th>
									<th>Ammount</th>
									<th>Delete</th>
								</tr>
							</thead>
							<tbody>
								{Inversment.map((data) => (
									<tr className='text-align-center' key={data.id}>
										<td>{data.name}</td>
										<td>{data.ammount}</td>
										<td>
											<button
												className='btn btn-danger'
												onClick={() => deleteInversmentData(data.id)}>
												Del
											</button>
										</td>
									</tr>
								))}
							</tbody>
						</table>
					</div>
				) : showData === 'cl' ? (
					<div>
						<p className='h5 p-3 text-primary'> </p>
						<table className='table table-stripped table-bordered'>
							<thead className='th-dark'>
								<tr>
									<th>Name</th>
									<th>Place</th>
								</tr>
							</thead>
							<tbody>
								{closedLoans.map((data) => (
									<tr className='text-align-center' key={data.id}>
										<td>{data.name}</td>
										<td>{data.place}</td>
									</tr>
								))}
							</tbody>
						</table>
					</div>
				) : (
					setShowData('cd')
				)}
			</div>
		</div>
	);
}
