import React from 'react';
import axios from 'axios';

const MailUrl = `auth/sendemail/`;
const auth = sessionStorage.getItem('access_token');

export default function MailData(props) {
	const Mail = async () => {
		const name = props.name;
		const cash = props.cash;
		const close = sessionStorage.getItem('cc');
		const email = props.email;
		const Data = await {
			name,
			email,
			close,
			cash,
		};
		await axios
			.post(`${MailUrl}${auth}`, Data)
			.then((res) => alert('Data Maild'));
	};
	return (
		<button onClick={Mail} className='btn btn-success'>
			Mail
		</button>
	);
}
