import React, { Component } from 'react';
import axios from 'axios';

import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
const Expence_server = `createexpence/`;
const Get_Expence_server = `getexpence/`;
const Delete_Expence = `deleteexpence/`;

class expences extends Component {
	constructor(props) {
		super(props);
		this.state = {
			date1: localStorage.getItem('date'),
			expenceName: '',
			expenceAmmount: '',
			expenceData: [],
		};
	}

	submitExpence = () => {
		let dailyExpence = {
			date: this.state.date1,
			expenceName: this.state.expenceName,
			expenceAmmount: this.state.expenceAmmount,
		};
		axios.post(Expence_server, dailyExpence);
		this.getExpenceData();
		toast.success('Expence Added successfully');
		this.clearData();
	};

	clearData = () => {
		this.setState({
			expenceName: '',
			expenceAmmount: '',
		});
	};

	setval = async (e) => {
		this.setState({
			expenceAmmount: e.target.value,
		});
	};

	getExpenceData = async () => {
		let expenceDatas;
		const res = await axios.get(Get_Expence_server);
		expenceDatas = await res.data.filter((data) =>
			data.date.includes(localStorage.getItem('date')),
		);
		await this.setState({
			expenceData: expenceDatas,
		});
	};

	componentDidMount() {
		this.setState({
			dailyExpence: {
				...this.state.dailyExpence,
				date: this.state.date1,
			},
		});
		this.getExpenceData();
	}

	deleteExpencedata = async (id) => {
		await axios.delete(`${Delete_Expence}${id}`);
		toast.error('Successfully Deleted Expence');
		this.getExpenceData();
	};

	render() {
		return (
			<div className='d-flex flex-row gap-3 justify-content-evenly mt-5'>
				<div className='d-flex flex-column align-left'>
					<a className='w-25 link-button' href='/reports'>
						Back
					</a>
					<h3 className='rounded-xl mt-3'>Date : {this.state.date1}</h3>
					<div
						style={{
							display: 'flex',
							flexDirection: 'column',
							height: '200px',
							justifyContent: 'space-evenly',
						}}>
						Expence Name:{' '}
						<select
							className='form-select'
							onChange={(e) =>
								this.setState({
									expenceName: e.target.value,
								})
							}>
							<option>Expense</option>
							<option>Tea</option>
							<option>FOOD</option>
							<option>PETROL/GAS</option>
							<option>SNACKS</option>
							<option>MEDICAL</option>
							<option>BIKE</option>
							<option>BIKE OIL CHANGE</option>
							<option>BIKE TIRE CHANGE</option>
							<option>BIKE CHAIN</option>
							<option>OTHERS</option>
						</select>
						<input
							type='text'
							className='form-control'
							placeholder='Ammount'
							onChange={this.setval}
							value={this.state.expenceAmmount}></input>
						<button className='btn btn-primary' onClick={this.submitExpence}>
							Add Expence 
						</button>
					</div>
				</div>
				<div>
					<table className='table table-striped table-bordered'>
						<thead className='th-dark'>
							<tr>
								<th>Expence Name</th>
								<th>Ammount</th>
								<th>Delete</th>
							</tr>
						</thead>
						<tbody>
							{this.state.expenceData.map((data) => (
								<tr>
									<td>{data.expenceName}</td>
									<td>{data.expenceAmmount}</td>
									<td>
										<button
											className='btn btn-danger'
											onClick={() => this.deleteExpencedata(data.id)}>
											X
										</button>
									</td>
								</tr>
							))}
						</tbody>
					</table>
				</div>
			</div>
		);
	}
}

export default expences;
