import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import styles from './login.module.css';
const authServer = `auth/login`;

export default function Login() {
	const [company, setCompany] = useState('');
	const [login, setLogin] = useState('');
	const [pass, setpass] = useState('');

	const delay = (ms) => new Promise((res) => setTimeout(res, ms));

	const setlogin = async (e) => {
		e.preventDefault();
		let accessToken;
		await localStorage.setItem('company', company);
		let AuthData = {
			username: login,
			password: pass,
		};
		await axios.post(authServer, AuthData).then((res) => {
			accessToken = res.data;
		});
		await sessionStorage.setItem('login', login);
		const { authKey } = accessToken;
		await sessionStorage.setItem('access_token', authKey);
		console.log(authKey);
		if (authKey === null || authKey === undefined) {
			toast.error('Invalid User information');
		} else {
			await toast.success(
				'Login success, Please while we redirect you to dashboard',
			);
			await toast.warning(
				'Redirecting to dashboard do not close or reload the browser',
			);
			await delay(6000);
			window.location.href = '/viewcostumers';
			//     window.open("/viewcostumers", "_black", "menubar=no, fullscreen=no, scrollbars=no,resizable=no,top=1000,left=1000,width=4000,height=4000");
		}
	};

	useEffect(() => {
		setCompany(localStorage.getItem('company'));
	}, []);

	const admincheck = async (e) => {
		if (login === 'admin') {
			e.preventDefault();
			let accessToken;
			await localStorage.setItem('company', company);
			let AuthData = {
				username: login,
				password: pass,
			};
			await axios.post(authServer, AuthData).then((res) => {
				accessToken = res.data;
			});
			await sessionStorage.setItem('login', login);
			const { authKey } = accessToken;
			await sessionStorage.setItem('access_token', authKey);
			console.log(authKey);
			if (authKey === null || authKey === undefined) {
				toast.error('Invalid User information');
			} else {
				await toast.success(
					'Login success, Please while we redirect you to dashboard',
				);
				await toast.warning(
					'Redirecting to dashboard do not close or reload the browser',
				);
				await sessionStorage.setItem('admin', 'admin');
				window.location.href = '/viewcostumers';
				//     window.open("/viewcostumers", "_black", "menubar=no, fullscreen=no, scrollbars=no,resizable=no,top=1000,left=1000,width=4000,height=4000");
			}
		} else {
			toast.error('You are Not An Admin Please Login as a normal user');
		}
	};

	return (
		<div className={styles.loginContainer}>
			<form onSubmit={setlogin} className={styles.loginForm}>
				<input
					type='text'
					onChange={(e) => setCompany(e.target.value)}
					placeholder='Company Name'
					className='form-control'
					defaultValue={company}
				/>
				<input
					type='text'
					onChange={(e) => setLogin(e.target.value)}
					className='form-control'
					placeholder='User Name'
				/>
				<input
					type='password'
					onChange={(e) => setpass(e.target.value)}
					className='form-control'
					placeholder='Password'
				/>
				<button type='submit' className='btn btn-primary'>
					Login
				</button>
				<button
					className='btn btn-outline-warning'
					style={{ margin: 5 }}
					onClick={admincheck}>
					Login as Admin
				</button>
			</form>
		</div>
	);
}
