import React, { Component } from 'react';
import axios from 'axios';
import './ccccollection.css';
const authKey = sessionStorage.getItem('access_token');
const CollectionList_Server = `getcollection/${authKey}/`;
const costumerServer = `getcostumer/${authKey}/`;

export default class Cotumercollection extends Component {
	constructor(props) {
		super(props);
		this.state = {
			date1: '',
			searchTerm: '',
			collectionData: [],
		};
	}

	changeDate = async (e) => {
		await this.setState({
			date1: e.target.value,
		});
	};

	setsearchCostumer = (e) => {
		this.setState({
			searchTerm: e.target.value,
		});
	};

	searchCostumerData = async () => {
		let collections;
		let costumerData;
		let loanDate;
		await axios.get(costumerServer).then((res) => {
			costumerData = res.data.filter((searchCostumer) =>
				searchCostumer.name.toLowerCase().includes(this.state.searchTerm),
			);
		});
		await costumerData.map((cd) => {
			return (loanDate = cd.loan_date);
		});
		loanDate = new Date(loanDate);
		loanDate = loanDate.toDateString();
		await axios.get(CollectionList_Server).then((res) => {
			return (collections = res.data);
		});
		var searchdata = collections.filter((collectionlist) =>
			collectionlist.costumer_name
				.toLowerCase()
				.includes(this.state.searchTerm.toLowerCase()),
		);
		let exactData = searchdata.filter((data1) => data1.date < loanDate);
		this.setState({
			collectionData: exactData,
		});
	};

	getCostumerdata = async () => {
		const searchidid = await sessionStorage.getItem('getcollection');
		console.log(searchidid);
		if (searchidid !== null) {
			let collections = [];
			await axios.get(CollectionList_Server).then((res) => {
				return (collections = res.data);
			});

			let data = [];

			collections.map((data11) => {
				if (data11.costumer_id.toString() === searchidid.toString()) {
					data.push(data11);
				}
			});
			this.setState({
				collectionData: data,
			});
			console.log(this.state.collectionData, 'forigin praveen');
		}
	};

	componentDidMount() {
		this.getCostumerdata();
	}

	render() {
		return (
			<div className='collectionDetails__'>
				<div className='d-flex flex-row gap-3 bg-light p-3 border'>
					<input
						type='text'
						id='outlined-search'
						onChange={this.setsearchCostumer}
						placeholder='Enter Costumer Name'
						className='collectionDetails__input'
					/>
					<button className='btn btn-warning' onClick={this.searchCostumerData}>
						 Search
					</button>
				</div>
				<div className='p-3'>
					<div className='shadow-sm table-wrapper-scroll-y my-custom-scrollbar'>
						<table className='table table-bordered border-dark'>
							<thead className='thead-light'>
								<tr>
									<th>Date</th>
									{this.state.searchTerm === '' && (
										<th className='collection_name_'>Costumer Name</th>
									)}
									<th>Ammount Collected</th>
								</tr>
							</thead>
							<tbody>
								{this.state.searchTerm === '' &&
									this.state.collectionData.map((data) => {
										return (
											<tr key={data.id}>
												<td>{data.date}</td>
												<td className='collection_name_'>
													{data.costumer_name}
												</td>
												<td>{data.ammount}</td>
											</tr>
										);
									})}
								{this.state.searchTerm !== '' &&
									this.state.collectionData.map((data) => {
										return (
											<tr key={data.id}>
												<td>{data.date}</td>
												<td>{data.ammount}</td>
											</tr>
										);
									})}
							</tbody>
						</table>
					</div>
				</div>
			</div>
		);
	}
}
