import React, { Component } from 'react';
import './reports.css';
import axios from 'axios';
import Plan from '../plan/plan.jsx';
import { Link } from 'react-router-dom';

const Expence_server = `getexpence/`;
const DL_SERVER = `getdl/`;
const Expence_server_total = `getexpencetotal/`;
const updateExpence_server_total = `updateexpencetotal/`;
const createExpence_server_total = `createexpencetotal/`;

class reports extends Component {
	constructor(props) {
		super(props);
		this.state = {
			date1: '',
			dailyExpence: {
				date: '',
				expenceName: '',
				expenceAmmount: '',
			},
			expenceTotal: '',
			dlammount: '',
		};
	}

	setDate = async (e) => {
		await this.setState({
			date1: e.target.value,
			dailyExpence: {
				...this.state.dailyExpence,
				date: e.target.value,
			},
		});
		await localStorage.setItem('date', this.state.date1);
		this.calculateExpancetotal();
		this.getDlammount();
		await localStorage.setItem('dl', this.state.dlammount);
		window.location.reload();
	};

	callfunction = async () => {
		await this.setState({
			date1: localStorage.getItem('date'),
		});
		await this.calculateExpancetotal();
		await this.getDlammount();
	};

	componentDidMount() {
		this.callfunction();
	}

	clearData = () => {
		this.setState({
			dailyExpence: {
				date: this.state.date1,
				expenceName: '',
				expenceAmmount: '',
			},
		});
	};

	submitExpence = () => {
		axios.post(Expence_server, this.state.dailyExpence);
		this.clearData();
	};

	calculateExpancetotal = async () => {
		let expenceDatas;
		let totalExpence = 0;
		await axios.get(Expence_server).then((res) => {
			expenceDatas = res.data.filter((data) =>
				data.date.includes(this.state.date1),
			);
		});
		await expenceDatas.map((data1) => {
			return (totalExpence =
				parseInt(data1.expenceAmmount) + parseInt(totalExpence));
		});
		this.setState({
			expenceTotal: totalExpence,
		});
		localStorage.setItem('expence', totalExpence);
	};

	addExpenceTotal = async () => {
		let date = this.state.date1;
		let expence_data;
		let id;
		await axios.get(Expence_server_total).then((res) => {
			expence_data = res.data.filter((data1) =>
				data1.date.includes(this.state.date1),
			);
		});
		await expence_data.map((data2) => {
			return (id = data2.id);
		});
		let expenceTotal = {
			date: date,
			ammount: this.state.expenceTotal,
		};
		await console.log(expence_data.length);
		if (expence_data.length === 1) {
			console.log(expenceTotal);
			axios.post(`${updateExpence_server_total}${id}/`, expenceTotal);
		} else {
			console.log(expenceTotal);
			axios.post(createExpence_server_total, expenceTotal).then((err) => {
				console.log(err);
			});
		}
		this.calculateExpancetotal();
	};

	getDlammount = async () => {
		let dldata;
		await axios
			.get(DL_SERVER)
			.then(
				(res) =>
					(dldata = res.data.filter((data) =>
						data.date.includes(localStorage.getItem('date')),
					)),
			);
		console.log(dldata);
		dldata.map((data1) => {
			return this.setState({
				dlammount: data1.ammount,
			});
		});
		localStorage.setItem('dl', this.state.dlammount);
	};

	render() {
		return (
			<div className=''>
				<div className='d-flex flex-row gap-2 w-50'>
					<h4>Date:</h4>
					<input
						type='date'
						className='form-control w-25'
						onChange={this.setDate}
						style={{ marginLeft: '10px' }}
						defaultValue={localStorage.getItem('date')}
					/>
				</div>
				<div className='d-flex flex-row gap-3 justify-content-evenly'>
					<div className='d-flex flex-column gap-3'>
						<div className='d-flex flex-column gap-3 mt-5'>
							<Link className='btn btn-outline-secondary' to='/addexpence'>
								Add Expence
							</Link>
							<button
								className='btn btn-outline-secondary'
								onClick={this.addExpenceTotal}>
								Update Expence 
							</button>
						</div>
						<div className='d-flex flex-column gap-3 text-success mt-3'>
							<h5>Dl Ammount: {this.state.dlammount}</h5>
							<h5>Expence Ammount: {this.state.expenceTotal}</h5>
						</div>
					</div>
					<div className='reports__plan'>
						<Plan
							date={this.state.date1}
							dl={this.state.dlammount}
							epa={this.state.expenceTotal}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default reports;
