import React, { useState } from 'react';
import axios from 'axios';
import './register.css';

const authServer = `auth/createuser`;

export default function RegisterComponent() {
	const [username, setName] = useState('');
	const [password, setPassword] = useState('');
	const [ispass, setIspass] = useState(true);
	const [email_address, setEmail] = useState('');

	const saveData = async (e) => {
		e.preventDefault();
		if (username !== '' && password !== '' && email_address !== '') {
			let data = { username, password, email_address };
			await axios.post(authServer, data).then(
				(res) =>
					alert(
						'User Created Successfully login with the user again to verify',
					),
				(err) => console.log(err, 'error occured'),
			);
			await sessionStorage.removeItem('login');
			await sessionStorage.removeItem('access_token');
			window.location.reload();
		} else {
			alert('Empty user cannot be created ');
		}
	};

	return (
		<form
			onSubmit={saveData}
			className='d-flex flex-column  gap-3 mb-1 w-75 align-middle'>
			<h4>Create New User</h4>
			<input
				type='text'
				onChange={(e) => setName(e.target.value)}
				placeholder='User Name'
				className='form-control'
			/>
			<input
				type='email'
				onChange={(e) => setEmail(e.target.value)}
				placeholder='User Email Id'
				className='form-control'
			/>
			<div className='d-flex gap-1 align-middle form-control justify-content-center'>
				<input
					type={ispass ? 'password' : 'text'}
					onChange={(e) => setPassword(e.target.value)}
					placeholder='User Login Password'
					className='password-input'
				/>
				{ispass ? (
					<i
						onClick={() => {
							setIspass(!ispass);
						}}
						className='bi bi-eye-fill h5'
					/>
				) : (
					<i
						onClick={() => {
							setIspass(!ispass);
						}}
						className='bi bi-eye-slash-fill h5'
					/>
				)}
			</div>
			<input
				value='Save User'
				className='btn btn-outline-primary'
				type='submit'
			/>
		</form>
	);
}
