import React, { Component } from 'react';
import LOGO from './LOGO.jpg';
import './settings.css';
import RegisterComponent from '../register/Register';
import RefreshComponent from '../refresh/refresh';

export default class Settings extends Component {
	constructor(props) {
		super(props);
		this.state = {
			company: 'Abipravi',
			email: '',
		};
	}
	setCompany = () => {
		localStorage.setItem('company', this.state.company);
		localStorage.setItem('email', this.state.email);
		window.location.reload();
	};

	componentDidMount() {
		const company = localStorage.getItem('company');
		this.setState({
			company,
		});
	}

	render() {
		return (
			<div className='d-flex flex-row justify-content-around'>
				<div className='d-flex flex-column'>
					<div Name='d-flex flex-column gap-3 w-50 flex-wrap'>
						<input
							type='text'
							onChange={(e) =>
								this.setState({
									company: e.target.value,
								})
							}
							className='form-control mt-3'
							placeholder='Enter Company Name'
						/>
						<input
							type='text'
							onChange={(e) =>
								this.setState({
									email: e.target.value,
								})
							}
							className='form-control mt-3'
							placeholder='Enter Company Email'
						/>
						<button
							onClick={this.setCompany}
							className='btn btn-primary w-20 m-3'>
							Save Company
						</button>
					</div>
					<div className='mt-4'>
						<h5>Company Name: {this.state.company}</h5>
						<h5>Software Version: 2.3.0</h5>
						<br />
						<a className='btn btn-primary' href='/addPlace'>
							Add Place
						</a>
					</div>
				</div>
				{sessionStorage.getItem('login') === 'admin' ? (
					<div
						style={{ padding: 20 }}
						className='w-50 d-flex justify-content-center shadow-sm'>
						<RegisterComponent />
					</div>
				) : (
					<p className='btn btn-dark h-25'>
						You not admin to create new user account
					</p>
				)}
			</div>
		);
	}
}
