import React from 'react';
import axios from 'axios';
import { NavLink } from 'react-router-dom';
import './plan.css';

const authKey = sessionStorage.getItem('access_token');
const COSTUMER_DETAILS_SERVER = `getcostumer/${authKey}/`;
const COSTUMERCLOSEHIGH = `createcloseup`;
const COSTUMERCLOSELOW = `createclosedown`;
const other_ammount_inServer = `createoai`;
const inversmentServer = `createinversment`;
const other_ammount_outServer = `createoao`;
const othersServer = `createothers`;

export class Plan extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			loanTotal: 0,
			loanIntrestTotal: 0,
			MStotal: 0,
			entryName: '',
			entryName1: '',
			entryAmmount: '',
			showField: false,
			showField1: false,
			closeCostumerName: [],
			lineup: '',
			linedown: '',
		};
	}

	loadData = async () => {
		this.get_Loan_Total();
		this.get_Loan_Intrest();
		this.get_Loan_MS();
	};

	get_Loan_Total = async () => {
		let loanData;
		let loanTotal = 0;
		await axios.get(COSTUMER_DETAILS_SERVER).then((res) => {
			loanData = res.data.filter((data) =>
				data.loan_date.includes(this.props.date),
			);
		});
		await loanData.map((data1) => {
			return (loanTotal = parseInt(loanTotal) + parseInt(data1.loanammount));
		});
		await this.setState({
			loanTotal,
		});
		localStorage.setItem('loan', loanTotal);
	};

	get_Loan_Intrest = async () => {
		let loanData;
		let loanIntrestTotal = 0;
		await axios.get(COSTUMER_DETAILS_SERVER).then((res) => {
			loanData = res.data.filter((data) =>
				data.loan_date.includes(this.props.date),
			);
		});
		await loanData.map((data1) => {
			return (loanIntrestTotal =
				parseInt(loanIntrestTotal) + parseInt(data1.loanInterst));
		});
		await this.setState({
			loanIntrestTotal,
		});
		localStorage.setItem('interst', loanIntrestTotal);
	};

	changeOption = async (e) => {
		await this.setState({
			entryName: e.target.value,
		});
		if (this.state.entryName !== ' ') {
			this.setNameField();
		}
	};
	changeOption1 = async (e) => {
		await this.setState({
			entryName1: e.target.value,
		});
		if (this.state.entryName1 !== ' ') {
			this.setNameField1();
		}
	};

	get_Loan_MS = async () => {
		let loanData;
		let MStotal = 0;
		await axios.get(COSTUMER_DETAILS_SERVER).then((res) => {
			loanData = res.data.filter((data) =>
				data.loan_date.includes(this.props.date),
			);
		});
		await loanData.map((data1) => {
			return (MStotal = parseInt(MStotal) + parseInt(data1.ms));
		});
		await this.setState({
			MStotal,
		});
		localStorage.setItem('ms', MStotal);
	};

	postEntryin = async () => {
		let date = this.props.date;
		const { entryName, entryAmmount, lineup } = this.state;

		if (entryName === ' ') {
			let postCostumerdata = {
				date: date,
				costumername: lineup,
				ammount: entryAmmount,
			};
			axios.post(COSTUMERCLOSEHIGH, postCostumerdata);
		} else if (entryName === ' ') {
			let postData = {
				date: date,
				reason: entryName,
				ammount: entryAmmount,
			};
			axios.post(`${other_ammount_inServer}`, postData);
		} else if (entryName === ' ') {
			let postData = {
				date: date,
				reason: 'Add Ammount',
				ammount: entryAmmount,
			};
			axios.post(`${other_ammount_inServer}`, postData);
		} else if (entryName === '  /  ') {
			let postData = {
				date: date,
				reason: lineup,
				ammount: entryAmmount,
			};
			axios.post(`${other_ammount_inServer}`, postData);
		} else if (entryName === ' ') {
			let postData = {
				date: date,
				name: lineup,
				ammount: entryAmmount,
			};
			axios.post(`${inversmentServer}`, postData);
		}
		this.setState({
			loneCloseCostumername: '',
			entryAmmount: '',
			reason: '',
		});
		window.location.reload();
	};

	postEntryout = async () => {
		let date = this.props.date;
		const { entryName1, entryAmmount, linedown } = this.state;

		if (entryName1 === ' ') {
			let postCostumerdata = {
				date: date,
				costumername: linedown,
				ammount: entryAmmount,
			};
			axios.post(COSTUMERCLOSELOW, postCostumerdata);
		} else if (entryName1 === ' ') {
			let postData = {
				date: date,
				reason: entryName1,
				ammount: entryAmmount,
			};
			axios.post(`${other_ammount_outServer}`, postData);
		} else if (entryName1 === ' /  ') {
			let postData = {
				date: date,
				reason: linedown,
				ammount: entryAmmount,
			};
			axios.post(`${othersServer}`, postData);
		} else if (entryName1 === ' ') {
			let postData = {
				date: date,
				reason: linedown,
				ammount: entryAmmount,
			};
			axios.post(`${other_ammount_outServer}`, postData);
		}
		this.setState({
			linedown: '',
			entryAmmount: '',
			reason: '',
		});
		window.location.reload();
	};

	setNameField = () => {
		this.setState({
			showField: true,
		});
	};

	setNameField1 = () => {
		this.setState({
			showField1: true,
		});
	};

	componentDidMount() {
		this.loadData();
	}

	render() {
		return (
			<div className='plan__'>
				<div className='plan__leftarea'>
					<div className='d-flex gap-3'>
						<button onClick={this.loadData} className='btn btn-primary'>
							Reload Data
						</button>
						<NavLink to='/editPlanCalc' className='btn btn-primary'>
							Edit Data
						</NavLink>
					</div>
					<div className='plan__area'>
						<div className='plan__area__items'>
							Total Loan: <h5>{this.state.loanTotal}</h5>
						</div>
						<div className='plan__area__items'>
							Loan Intrest: <h5>{this.state.loanIntrestTotal}</h5>
						</div>
						<div className='plan__area__items'>
							Loan MS: <h5>{this.state.MStotal}</h5>
						</div>
					</div>
					<div className='d-flex gap-2 w-100'>
						<div className='d-flex flex-column gap-3'>
							<select
								name='inputMaster'
								className='form-control'
								onChange={this.changeOption}>
								<option>Ammount In</option>
								<option> </option>
								<option> </option>
								<option> </option>
								<option>  /  </option>
								<option> </option>
							</select>
							{this.state.showField ? (
								<div>
									<input
										type='text'
										placeholder='Enter  Name'
										onChange={(e) =>
											this.setState({
												lineup: e.target.value,
											})
										}
										className='form-control'
										value={this.state.lineup}></input>
								</div>
							) : (
								<br />
							)}
							<input
								type='text'
								onChange={(e) =>
									this.setState({
										entryAmmount: e.target.value,
									})
								}
								placeholder='Enter Ammount'
								className='form-control'
								value={this.state.entryAmmount}
							/>
							<br />
							<button
								className='btn btn-outline-primary'
								onClick={this.postEntryin}>
								Add
							</button>
						</div>
						<div className='d-flex flex-column gap-3'>
							<select
								name='inputMaster'
								className='form-control'
								onChange={this.changeOption1}>
								<option>Ammount Out</option>
								<option> </option>
								<option> </option>
								<option> /  </option>
								<option> </option>
							</select>
							{this.state.showField1 ? (
								<div>
									<input
										type='text'
										placeholder='Enter  Name'
										onChange={(e) =>
											this.setState({
												linedown: e.target.value,
											})
										}
										className='form-control'
										value={this.state.linedown}></input>
								</div>
							) : (
								<br />
							)}
							<input
								type='text'
								onChange={(e) =>
									this.setState({
										entryAmmount: e.target.value,
									})
								}
								className='form-control'
								placeholder='Enter Ammount'
								value={this.state.entryAmmount}
							/>
							<br />
							<button
								className='btn btn-outline-primary'
								onClick={this.postEntryout}>
								Add
							</button>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Plan;
